.navigation_wrapper {
  position: fixed;
  width: 100%;
  height: 70px;
  z-index: 10;
  overflow: hidden;
}
.nav-active {
  height: 100%;
}
nav {
  position: absolute;
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 70px;
  align-items: center;
  z-index: 1;
}
.header-logo {
  font-style: italic;
  font-family: "Fira Sans Extra Condensed", sans-serif;
  font-weight: bold;
  border-bottom: 4px solid #303036;
  display: inline-block;
  -webkit-filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
          filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  margin-left: 30px;
}
.nav-active .header-logo {
  border-bottom: 4px solid #fff;
  color: #fff;
}
.hamberger {
  height: 21px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-right: 30px;
  cursor: pointer;
}
nav .hamberger .line {
  width: 40px;
  height: 5px;
  background-color: #303036;
  -webkit-filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
          filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}
.nav-active .hamberger .line {
  background-color: #fff;
  /* transform: translateY(150%) rotate(45deg); */
  -webkit-animation-name: line1;
          animation-name: line1;
  -webkit-animation-duration: 0.75s;
          animation-duration: 0.75s;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
}
.nav-active .hamberger .line:nth-child(2) {
  /* transform: translateY(-150%) rotate(-45deg); */
  -webkit-animation-name: line2;
          animation-name: line2;
  -webkit-animation-duration: 0.75s;
          animation-duration: 0.75s;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
}
.navigation_wrapper .menu-bg {
  transition: all 2s;
  position: absolute;
  width: 100vw;
  height: 100vw;
  opacity: 0;
  display: none;
  transform-origin: 50% 50%;
  transform: translate(90%, -90%) scale(1.5);
  border-radius: 100%;
  background-color: #303036;
}
.nav-active .menu-bg {
  transition: all 2s;
  right: 0%;
  top: 0%;
  opacity: 1;
  display: block;
  -webkit-animation-name: nav;
          animation-name: nav;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-timing-function: ease-in;
          animation-timing-function: ease-in;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
}
@-webkit-keyframes nav {
  0% {
    opacity: 0;
    transform: translate(90%, -90%) scale(1.5);
  }
  60% {
    opacity: 1;
  }
  100% {
    opacity: 1;
    transform: translate(0%, 0%) scale(1.5);
  }
}
@keyframes nav {
  0% {
    opacity: 0;
    transform: translate(90%, -90%) scale(1.5);
  }
  60% {
    opacity: 1;
  }
  100% {
    opacity: 1;
    transform: translate(0%, 0%) scale(1.5);
  }
}
@-webkit-keyframes line1 {
  0% {
    transform: translateY(0%) rotate(0deg);
  }
  50% {
    transform: translateY(150%) rotate(0deg);
  }
  100% {
    transform: translateY(150%) rotate(45deg);
  }
}
@keyframes line1 {
  0% {
    transform: translateY(0%) rotate(0deg);
  }
  50% {
    transform: translateY(150%) rotate(0deg);
  }
  100% {
    transform: translateY(150%) rotate(45deg);
  }
}
@-webkit-keyframes line2 {
  0% {
    transform: translateY(0%) rotate(0deg);
  }
  50% {
    transform: translateY(-150%) rotate(0deg);
  }
  100% {
    transform: translateY(-150%) rotate(-45deg);
  }
}
@keyframes line2 {
  0% {
    transform: translateY(0%) rotate(0deg);
  }
  50% {
    transform: translateY(-150%) rotate(0deg);
  }
  100% {
    transform: translateY(-150%) rotate(-45deg);
  }
}

